.vayu-blocks-advance-slider .slick-track{
    display: flex !important;
    align-items: center;
     //justify-content: center;
}

.vayu_blocks_duotone-filters{
    display:none;
}

.slide-active {
    opacity: 0; /* Initially hide the element */
    animation: fadeInUp 2.5s cubic-bezier(0.42, 0, 0.58, 1) forwards; /* Custom cubic-bezier for smoother animation */
    animation-delay: -1s; /* Negative delay to start animation slightly early */
}

/* Common animation class */
.vayu-blocks-animate-on-slide {
    opacity: 0; /* Initially hidden */
    animation: fadeInUp 0.7s cubic-bezier(0.42, 0, 0.58, 1) forwards;
    animation-delay: -0.3s;
}

.vayu-blocks-animate-on-slide-down{
    opacity: 0; /* Initially hidden */
    animation: fadeInDown 0.7s cubic-bezier(0.42, 0, 0.58, 1) forwards;
    animation-delay: -0.3s;
}

.vayu-blocks-animate-on-slide-left{
    opacity: 0; /* Initially hidden */
    animation: fadeInLeft 0.7s cubic-bezier(0.42, 0, 0.58, 1) forwards;
    animation-delay: -0.3s;
}

.vayu-blocks-animate-on-slide-right{
    opacity: 0; /* Initially hidden */
    animation: fadeInRight 0.7s cubic-bezier(0.42, 0, 0.58, 1) forwards;
    animation-delay: -0.3s;
}

.vayu_blocks_heading,
.vayu_blocks_sub_heading,
.vayu_blocks_slider_button1,
.vayu_blocks_slider_button2{
    opacity:0;
}

/* Apply delays to ensure sequential animation */
.vayu_blocks_heading.vayu-blocks-animate-on-slide {
    animation-delay: 0s; /* First element */
}

.vayu_blocks_sub_heading.vayu-blocks-animate-on-slide {
    animation-delay: 0.5s; /* Second element */
}

.vayu_blocks_slider_button1.vayu-blocks-animate-on-slide {
    animation-delay: 1s; /* Third element */
}

.vayu_blocks_slider_button2.vayu-blocks-animate-on-slide {
    animation-delay: 1s; /* Fourth element */
}

/* Apply delays to ensure sequential animation */
.vayu_blocks_heading.vayu-blocks-animate-on-slide-down {
    animation-delay: 0s; /* First element */
}

.vayu_blocks_sub_heading.vayu-blocks-animate-on-slide-down {
    animation-delay: 0.5s; /* Second element */
}

.vayu_blocks_slider_button1.vayu-blocks-animate-on-slide-down {
    animation-delay: 1s; /* Third element */
}

.vayu_blocks_slider_button2.vayu-blocks-animate-on-slide-down {
    animation-delay: 1s; /* Fourth element */
}
/* Apply delays to ensure sequential animation */
.vayu_blocks_heading.vayu-blocks-animate-on-slide-left {
    animation-delay: 0s; /* First element */
}

.vayu_blocks_sub_heading.vayu-blocks-animate-on-slide-left {
    animation-delay: 0.5s; /* Second element */
}

.vayu_blocks_slider_button1.vayu-blocks-animate-on-slide-left {
    animation-delay: 1s; /* Third element */
}

.vayu_blocks_slider_button2.vayu-blocks-animate-on-slide-left {
    animation-delay: 1s; /* Fourth element */
}
/* Apply delays to ensure sequential animation */
.vayu_blocks_heading.vayu-blocks-animate-on-slide-right {
    animation-delay: 0s; /* First element */
}

.vayu_blocks_sub_heading.vayu-blocks-animate-on-slide-right {
    animation-delay: 0.5s; /* Second element */
}

.vayu_blocks_slider_button1.vayu-blocks-animate-on-slide-right {
    animation-delay: 1s; /* Third element */
}

.vayu_blocks_slider_button2.vayu-blocks-animate-on-slide-right {
    animation-delay: 1s; /* Fourth element */
}

@keyframes fadeInUp {
    0% {
        opacity: 0;
        transform: translateY(20px); /* Start slightly lower */
    }
    100% {
        opacity: 1;
        transform: translateY(0); /* End at the original position */
    }
}

@keyframes fadeInDown {
    from {
        opacity: 0;
        transform: translateY(-20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes fadeInLeft {
    from {
        opacity: 0;
        transform: translateX(-20px);
    }
    to {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes fadeInRight {
    from {
        opacity: 0;
        transform: translateX(20px);
    }
    to {
        opacity: 1;
        transform: translateX(0);
    }
}